@import "../../../styles/settings.scss";

.root {
    padding: 3rem 0;
    position: fixed;
    top: 20%;
    right: 3%;
    background: $grey-color;
    z-index: 99;
    
    .close {
        cursor: pointer;
        font-size: 35px;
        position: absolute;
        top: -10px;
        right: 15px;
      }

    .imageWrapper {
      width: 100%;
      height: 400px;
      padding: 0 10px 0 10px;
      position: relative;
  
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
      .button {
        position: absolute;
        top: 85%;
        left: 80%;
      }
    }
    .galleryWrapper {
      display: flex;
      flex-direction: row;
      max-height: 100px;
      padding: 0 10px 0 10px;
      position: relative;
      margin-bottom: 20px;
  
      & > div {
        margin: 5px;
        cursor: pointer;
      }
  
      .arrowLeft {
        position: absolute;
        top: 40%;
        left: 0%;
      }
      .arrowRight {
        top: 40%;
        right: 0%;
        position: absolute;
      }
      .gallery {
        justify-content: space-between;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        max-height: 100px;
  
        .thumbnail {
          flex: 1;
          min-height: 100px;
          padding: 5px;
  
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
      }
    }
  
    .rating {
      div {
        color: $silver-color;
        font-size: 12px;
        p {
          margin: 10px 5px 0 0;
        }
      }
    }
  
    .line {
      border-bottom: 1px solid $silver-color;
      margin: 5px;
    }
  
    .price {
      margin: 5px;
      .oldPrice {
        margin: 5px;
        color: $silver-color;
        font-weight: 600;
        padding: 0 5px;
        text-decoration: line-through;
      }
      .newPrice {
        margin: 5px 5px 5px 30px;
        background-color: $black-color;
        color: $white-color;
        padding: 0 5px;
        font-weight: 600;
      }
    }
    .actions {
      margin: 5px;
    }
    .amount {
      margin: 5px;
      p {
        padding: 0 5px;
        font-weight: 600;
        margin-top: 10px;
      }
      .input {
        height: 40px;
        width: 70px;
        margin: 5px;
      }
      .button {
        height: 40px;
        width: 40px;
        margin: 5px;
      }
    }
    .overview {
      margin: 0 5px;
      p:nth-child(2) {
        color: $silver-color;
        margin-left: 5px;
      }
    }
    .socialMedia {
      margin: 0 5px;
    }
  }